<template>
  <div class="tags-content">
    <el-card class="box-card">
      <!-- 面包屑 -->
      <div slot="header" class="clearfix" v-if="$route.query.name">
        <el-breadcrumb separator-class="el-icon-arrow-right">
          <el-breadcrumb-item>学科管理</el-breadcrumb-item>
          <el-breadcrumb-item>{{ $route.query.name }}</el-breadcrumb-item>
          <el-breadcrumb-item>标签管理</el-breadcrumb-item>
        </el-breadcrumb>
      </div>
      <!-- 搜索框 -->
      <el-row>
        <el-col :span="18">
          <el-form
            :model="searchForm"
            size="small"
            style="display: flex"
            ref="searchFormDom"
          >
            <el-form-item label="标签名称" label-width="80px">
              <el-input v-model.trim="searchForm.tagName"></el-input>
            </el-form-item>
            <el-form-item label="状态" label-width="80px">
              <el-select v-model="searchForm.state" placeholder="请选择">
                <el-option label="启用" value="1"></el-option>
                <el-option label="禁用" value="0"></el-option>
              </el-select>
            </el-form-item>
            <el-form-item style="margin-left: 10px">
              <el-button @click="searchForm.tagName = searchForm.state = ''"
                >清除</el-button
              >
              <el-button type="primary" @click="secrchClick">搜索</el-button>
            </el-form-item>
          </el-form>
        </el-col>
        <el-col :span="6" style="text-align: right">
          <el-button
            type="text"
            icon="el-icon-back"
            v-show="$route.query.name"
            @click="$router.push('list')"
            >返回学科</el-button
          >
          <el-button
            type="success"
            icon="el-icon-edit"
            size="small"
            @click="open('add', '')"
            >新增标签</el-button
          >
        </el-col>
      </el-row>
      <el-alert
        :title="title"
        type="info"
        :closable="false"
        show-icon
        v-if="title !== ''"
      ></el-alert>
      <!-- 表格 -->
      <el-table :data="tableData" style="width: 100%">
        <el-table-column type="index" label="序号" width="80"></el-table-column>
        <el-table-column prop="subjectName" label="所属学科"></el-table-column>
        <el-table-column prop="tagName" label="标签名称"></el-table-column>
        <el-table-column prop="username" label="创建者"></el-table-column>
        <el-table-column prop="addDate" label="创建日期">
          <template #default="{ row }">
            <span>{{ row.addDate | parseTimeByString() }}</span>
          </template>
        </el-table-column>
        <el-table-column prop="state" label="状态">
          <template #default="{ row }">
            <span>{{ row.state === 1 ? "已启用" : "已禁用" }}</span>
          </template>
        </el-table-column>
        <el-table-column label="操作" width="150">
          <template #default="{ row }">
            <el-button
              type="text"
              size="small"
              @click="disableClick(row.state, row.id)"
            >
              <template #default v-if="row.state === 1"> 禁用 </template>
              <template #default v-else>启用 </template>
            </el-button>
            <el-button
              type="text"
              size="small"
              :disabled="row.state === 1"
              @click="open('edit', row)"
              >修改</el-button
            >
            <el-button
              type="text"
              size="small"
              :disabled="row.state === 1"
              @click="del(row.id)"
              >删除</el-button
            >
          </template>
        </el-table-column>
      </el-table>
      <!-- 分页器 -->
      <el-pagination
        background
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="pageObj.page"
        :page-sizes="[5, 10, 20, 30]"
        :page-size="10"
        layout=" prev, pager, next, sizes,jumper"
        :total="total"
        hide-on-single-page
      >
      </el-pagination>
      <!-- 对话框 -->
      <el-dialog
        :title="titleText"
        :visible.sync="dialogFormVisible"
        width="400px"
        :before-close="cancel"
      >
        <el-form
          :model="addForm"
          label-width="80px"
          :rules="rules"
          ref="addFormDom"
        >
          <el-form-item label="所属学科" prop="subjectID">
            <el-select
              v-model="addForm.subjectID"
              placeholder="请选择"
              style="width: 100%"
            >
              <el-option
                :label="item.label"
                :value="item.value"
                v-for="item in selectValue"
                :key="item.value"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="目录名称" prop="tagName">
            <el-input
              v-model="addForm.tagName"
              autocomplete="off"
              placeholder="请输入目录名称"
            ></el-input>
          </el-form-item>
        </el-form>
        <div slot="footer" class="dialog-footer">
          <el-button @click="cancel">取 消</el-button>
          <el-button type="primary" @click="determine">确 定</el-button>
        </div>
      </el-dialog>
    </el-card>
  </div>
</template>

<script>
import {
  list,
  add,
  changeState,
  update,
  remove
} from '../../api/hmmm/tags'
import { simple } from '../../api/hmmm/subjects'

import PageTool from '../../module-manage/components/page-tool.vue'
export default {
  comments: {
    PageTool
  },
  computed: {
    titleText () {
      let txt = ''
      this.typeDialog === 'add' ? (txt = '新增目录') : (txt = '修改目录')
      return txt
    }
  },
  watch: {
    $route: function (to, from) {
      // 执行需要更新的方法
      this.getList(this.pageObj)
    }
  },
  data () {
    return {
      pageObj: {
        page: 1, // 当前页数
        pagesize: 10 // 每页数据
      },
      title: '',
      searchForm: {
        tagName: '',
        state: '' // 状态 1 开启 0 屏蔽
      },
      tableData: [],
      total: 0,
      addForm: {
        subjectID: '',
        tagName: ''
      },
      dialogFormVisible: false,
      rules: {
        tagName: [
          { required: true, message: '请输入标签名称', trigger: 'blur' },
          {
            min: 1,
            max: 16,
            message: '长度在 1 到 16 个字符',
            trigger: 'blur'
          }
        ],
        subjectID: [
          { required: true, message: '请选择所属学科', trigger: 'blur' }
        ]
      },
      selectValue: [],
      updateId: '', // 学科id
      typeDialog: ''
    }
  },
  async created () {
    this.$route.query.id
      ? this.getList({ ...this.pageObj, subjectID: this.$route.query.id })
      : this.getList(this.pageObj)
    const { data: res } = await simple()
    // console.log(res)
    this.selectValue = res
  },
  methods: {
    // 修改状态
    async disableClick (state, id) {
      state = state ? 0 : 1
      await changeState({ id, state })
      this.$message.success('操作成功')
      this.getList(this.pageObj)
    },
    // 获取列表数据
    async getList (params) {
      const { data } = await list(params)
      this.tableData = data.items
      this.total = data.counts
      this.title = `数据一共 ${this.total} 条`
    },
    // 每页数据改变调用的函数
    handleSizeChange (val) {
      this.pageObj.pagesize = val
      this.pageObj.page = 1
      this.searchList()
    },
    // 页码改变的函数
    handleCurrentChange (val) {
      this.pageObj.page = val
      this.searchList()
    },
    // 打开对话框
    async open (type, value) {
      this.typeDialog = type
      this.updateId = value.id
      this.dialogFormVisible = true
      if (type === 'edit') {
        this.addForm.tagName = value.tagName
        this.addForm.subjectID = value.subjectID
      }
    },
    // 对话框取消按钮
    cancel () {
      this.addForm.tagName = ''
      this.addForm.subjectID = ''
      this.dialogFormVisible = false
    },
    // 对话框确认按钮
    async determine () {
      await this.$refs.addFormDom.validate()
      if (this.typeDialog === 'add') {
        await add(this.addForm)
      } else {
        await update({ id: this.updateId, ...this.addForm })
      }
      this.$message.success('操作成功')
      this.getList(this.pageObj)
      this.cancel()
    },
    // 删除
    del (id) {
      this.$confirm('此操作将永久删除该文件, 是否继续?', '提示', {
        confirmButtonText: '确定',
        cancelButtonText: '取消',
        type: 'warning'
      })
        .then(async () => {
          await remove({ id })
          this.$message.success('删除成功')
          this.getList(this.pageObj)
        })
        .catch(() => {})
    },
    // 搜索
    secrchClick () {
      this.pageObj.page = 1
      this.pageObj.pagesize = 10
      if (!this.$route.query.id) {
        this.searchList()
      }
    },
    searchList () {
      if (this.searchForm.tagName) {
        this.getList({
          ...this.pageObj,
          tagName: this.searchForm.tagName
        })
      } else if (this.searchForm.state) {
        this.getList({
          ...this.pageObj,
          state: this.searchForm.state
        })
      } else if (this.searchForm.tagName && this.searchForm.state) {
        this.getList({
          ...this.pageObj,
          ...this.searchForm
        })
      } else {
        this.getList(this.pageObj)
      }
    }
  }
}
</script>

<style scoped rel="stylesheet/scss" lang="scss">
.tags-content {
  padding: 10px;
  .el-form-item {
    font-size: 12px;
    .el-input__inner {
      width: 200px;
      height: 32px;
    }
  }
  .el-pagination {
    text-align: right;
    margin-top: 20px;
  }
  .dialog-footer {
    text-align: right;
  }
}
</style>
